.footer {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #A9A9A9;
  margin-top: 30%;
  border-top: 1px solid #A9A9A9;
  text-align: center;
  width : calc(100% * 300 / 390);
  margin-inline: auto;
}

@media (min-width : 720px) {
  .footer {
    display : grid;
    grid-template-columns: 1fr 1fr;
    border: none;
    margin: 0;
    padding-top: 20%;
  }

  .footer__paragraph {
    grid-column: 2/3;
  }
}